*,
*:before,
*:after {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
}

body {
   height: 100vh;

   display: flex;
   align-items: center;
   justify-content: center;

   background: #555;
   color: #fff;
}

.spinner__link {
   display: flex;
   align-items: center;
   justify-content: center;

   img {
      width: 100%;
      height: 100%;
   }

   background: linear-gradient(
      0deg,
      #5552 1%,
      rgba(141, 120, 138, 0.9) 10%,
      rgba(141, 120, 138, 0.9) 90%,
      #5552 99%
   );
}

.spinner1 {
   font-size: 100%;

   .spinner__link {
      border-radius: 5px;
   }
}

.spinner2 {
   font-size: 30%;
}
